body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* a, a {
  all: unset;
  color: black;
  text-decoration: none;
}

*, *:hover {
  cursor: url("/portfolio/media/img/cursor_circle.png"), auto;
}

@media (pointer: none), (pointer: coarse) {
  .custom-cursor {
    display: none;
  }
}

.custom-cursor {
  z-index: 100;
  width: 10px;
  height: 10px;
  position: fixed;
  top: 50%;
  left: 50%;
  border-radius: 100%;
  pointer-events: none;
  transition: width 0.3s, height 0.3s;
  -webkit-animation: cursorFadeIn 0.6s linear 1;
          animation: cursorFadeIn 0.6s linear 1;
}

@-webkit-keyframes cursorFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes cursorFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.custom-cursor div {
  content: '';
  width: 300%;
  height: 300%;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid transparent;
  border-radius: inherit;
  backdrop-filter: invert(0) saturate(1);
  -webkit-backdrop-filter: invert(0) saturate(1);
  transition: border-color 0.3s, -webkit-backdrop-filter 0.3s;
  transition: backdrop-filter 0.3s, border-color 0.3s;
  transition: backdrop-filter 0.3s, border-color 0.3s, -webkit-backdrop-filter 0.3s;
  -moz-transition: backdrop-filter 0.3s, border-color 0.3s;
  -webkit-transition: backdrop-filter 0.3s, border-color 0.3s;
  -o-transition: backdrop-filter 0.3s, border-color 0.3s;
}

*::selection {
  background-color: pink;
}

::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  background-color: white;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: none;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: none;
  border-radius: 10px;
}

