    background-repeat: no-repeat;
    background-size: 70%;
    transition: all 0.3s, background-image 0.2s ease-in-out;
  }
  #chkNav:checked ~ .nav-icon {
    background: url("/portfolio/media/img/nav-icon-open.png"), black;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 70%;
  }
  .nav-list {
    background-color: #9da7d4;
    min-height: 100vh;
    flex-basis: 100vw;
    display: flex;
    flex-direction: column;
    transform: translateX(100%);
    flex-wrap: wrap;
    align-content: flex-start;
    justify-content: space-around;
    transition: all 0.5s;
    text-align: left;
    justify-content: center;
    pointer-events: auto;
    opacity: 1;
  }
  #chkNav:checked ~ .nav-list {
    transform: translateX(0%);
