  .nav-list li {
    transition: all 0.2s;
    color: white;
    font-size: 1.2em;
    padding-left: 10vw;
    pointer-events: auto;
  }
  .nav-list li a {
    color: white;
  }
  .nav-list li:hover {
    opacity: 0.5;
  }
  .nav-list li.nav-option {
    flex-basis: 100%;
    text-align: left;
    line-height: calc(3rem + 1vw);
  }
  .nav-list li.nav-option a {
